"use client"

import { useState, useEffect } from "react"
import {
  Bell,
  Mail,
  User,
  Search,
  Filter,
  Calendar,
  Clock,
  MapPin,
  Users,
  Edit,
  Trash2,
  Download,
  Plus,
  Sun,
} from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"

interface StudentGroup {
  idGroupe: number
  nomGroupe: string
  idEtudiant: number
  etudiant: string
  idSoutenance: number
  noteRapport?: number
  notePresentation?: number
  noteDiscussion?: number
  noteSavoirFaireSavoirEtre?: number
  noteTotale?: number
  remarque?: string
}

interface Soutenance {
  id: number
  date: string
  heure: string
  lieu: string
  groupes: string[]
  jury: string[]
  statut: string
}

export default function EvaluationPage() {
  const [students, setStudents] = useState<StudentGroup[]>([])
  const [soutenances, setSoutenances] = useState<Soutenance[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [updating, setUpdating] = useState<number | null>(null)
  const [successMessage, setSuccessMessage] = useState<string | null>(null)
  const [validationErrors, setValidationErrors] = useState<Record<string, string>>({})
  const [darkMode, setDarkMode] = useState(false)
  const [searchQuery, setSearchQuery] = useState("")
  const idEncadrant = 1 // This could come from auth context

  useEffect(() => {
    async function fetchData() {
      try {
        setLoading(true)
        
        // Simulate API calls
        const [studentsResponse, soutenancesResponse] = await Promise.all([
          fetchStudentsData(),
          fetchSoutenancesData()
        ])

        const studentsWithTotal = studentsResponse.map((student: StudentGroup) => ({
          ...student,
          noteTotale: calculateTotal(student),
        }))

        setStudents(studentsWithTotal)
        setSoutenances(soutenancesResponse)
      } catch (error: any) {
        setError(error.message || "Une erreur est survenue lors du chargement des données")
      } finally {
        setLoading(false)
      }
    }
    fetchData()
  }, [])

  // Simulated API functions
  const fetchStudentsData = async (): Promise<StudentGroup[]> => {
    // Replace with real API call
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve([])
      }, 1000)
    })
  }

  const fetchSoutenancesData = async (): Promise<Soutenance[]> => {
    // Replace with real API call
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve([])
      }, 1000)
    })
  }

  const calculateTotal = (student: StudentGroup): number | undefined => {
    const grades = [
      student.noteRapport,
      student.notePresentation,
      student.noteDiscussion,
      student.noteSavoirFaireSavoirEtre,
    ].filter((grade): grade is number => grade !== undefined && !isNaN(grade))

    if (grades.length === 0) return undefined
    const sum = grades.reduce((acc, grade) => acc + grade, 0)
    return Number((sum / grades.length).toFixed(2))
  }

  const validateGrade = (value: string, index: number, field: string): boolean => {
    const numValue = Number(value)
    const errorKey = `${index}-${field}`

    if (value && (isNaN(numValue) || numValue < 0 || numValue > 20)) {
      setValidationErrors((prev) => ({ ...prev, [errorKey]: "La note doit être entre 0 et 20" }))
      return false
    } else {
      setValidationErrors((prev) => {
        const newErrors = { ...prev }
        delete newErrors[errorKey]
        return newErrors
      })
      return true
    }
  }

  const handleInputChange = (index: number, field: string, value: string | number) => {
    const gradeFields = ["noteRapport", "notePresentation", "noteDiscussion", "noteSavoirFaireSavoirEtre"]

    if (gradeFields.includes(field)) {
      if (!validateGrade(value.toString(), index, field)) return
    }

    const updatedStudents = [...students]
    const parsedValue = field === "remarque" ? value : value === "" ? undefined : Number(value)
    // @ts-ignore
    updatedStudents[index][field] = parsedValue

    if (gradeFields.includes(field)) {
      updatedStudents[index].noteTotale = calculateTotal(updatedStudents[index])
    }

    setStudents(updatedStudents)
  }

  const handleSave = async (index: number) => {
    if (updating !== null || Object.keys(validationErrors).length > 0) return
    setUpdating(index)
    setError(null)
    setSuccessMessage(null)

    try {
      // Simulate API call
      await new Promise((resolve) => setTimeout(resolve, 1000))
      setSuccessMessage("Évaluation sauvegardée avec succès!")
      window.scrollTo({ top: 0, behavior: "smooth" })
    } catch (error: any) {
      setError(error.message || "Erreur lors de la sauvegarde")
      window.scrollTo({ top: 0, behavior: "smooth" })
    } finally {
      setUpdating(null)
    }
  }

  const handleUpdate = async (index: number) => {
    if (updating !== null || Object.keys(validationErrors).length > 0) return
    setUpdating(index)
    setError(null)
    setSuccessMessage(null)

    try {
      // Simulate API call
      await new Promise((resolve) => setTimeout(resolve, 1000))
      setSuccessMessage("Évaluation mise à jour avec succès!")
      window.scrollTo({ top: 0, behavior: "smooth" })
    } catch (error: any) {
      setError(error.message || "Erreur lors de la mise à jour")
      window.scrollTo({ top: 0, behavior: "smooth" })
    } finally {
      setUpdating(null)
    }
  }

  const toggleDarkMode = () => {
    setDarkMode(!darkMode)
    document.documentElement.classList.toggle("dark")
  }

  const filteredSoutenances = soutenances.filter(soutenance =>
    soutenance.groupes.some(g => g.toLowerCase().includes(searchQuery.toLowerCase())) ||
    soutenance.jury.some(j => j.toLowerCase().includes(searchQuery.toLowerCase())) ||
    soutenance.lieu.toLowerCase().includes(searchQuery.toLowerCase())
  )

  if (loading)
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary"></div>
      </div>
    )

  return (
    <div className={darkMode ? "dark" : ""}>
      <div className="min-h-screen bg-white dark:bg-gray-900">
        <header className="sticky top-0 z-10 bg-[#1e2a3b] text-white shadow-md">
          <div className="container mx-auto px-4 py-3 flex justify-between items-center">
            <h1 className="text-xl font-bold">My Application</h1>
            <div className="flex items-center space-x-4">
              <button onClick={toggleDarkMode} className="p-2 rounded-full hover:bg-[#2a3a50] transition-colors">
                <Sun className="h-5 w-5" />
              </button>
              <button className="p-2 rounded-full hover:bg-[#2a3a50] transition-colors relative">
                <Bell className="h-5 w-5" />
              </button>
              <button className="p-2 rounded-full hover:bg-[#2a3a50] transition-colors relative">
                <Mail className="h-5 w-5" />
              </button>
              <button className="p-2 rounded-full hover:bg-[#2a3a50] transition-colors">
                <User className="h-5 w-5" />
              </button>
            </div>
          </div>
        </header>

        <div className="flex">
          <aside className="hidden md:block w-64 bg-[#1e2a3b] text-white h-[calc(100vh-64px)] sticky top-16">
            <nav className="p-4">
              <ul className="space-y-2">
                <li><a href="#" className="block p-2 rounded hover:bg-[#2a3a50] transition-colors">Dashboard</a></li>
                <li><a href="#" className="block p-2 rounded bg-[#2a3a50] font-medium">Soutenances</a></li>
                <li><a href="#" className="block p-2 rounded hover:bg-[#2a3a50] transition-colors">Étudiants</a></li>
                <li><a href="#" className="block p-2 rounded hover:bg-[#2a3a50] transition-colors">Groupes</a></li>
                <li><a href="#" className="block p-2 rounded hover:bg-[#2a3a50] transition-colors">Paramètres</a></li>
              </ul>
            </nav>
          </aside>

          <main className="flex-1 p-6">
            <div className="max-w-7xl mx-auto">
              <div className="flex items-center mb-6">
                <div className="mr-4 p-2 bg-[#f0f0f0] dark:bg-gray-800 rounded-lg">
                  <svg className="h-8 w-8 text-[#b27c5a]" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <path d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
                  </svg>
                </div>
                <h1 className="text-2xl font-bold text-gray-900 dark:text-white">Liste des Soutenances</h1>
              </div>

              {successMessage && (
                <div className="mb-6 p-4 bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-100 rounded-lg">
                  {successMessage}
                </div>
              )}
              {error && (
                <div className="mb-6 p-4 bg-red-100 dark:bg-red-900 text-red-700 dark:text-red-100 rounded-lg">
                  {error}
                </div>
              )}

              <Card className="mb-6">
                <CardHeader>
                  <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
                    <div>
                      <CardTitle className="text-2xl text-[#b27c5a]">Soutenances</CardTitle>
                      <CardDescription>Gestion des sessions de soutenance</CardDescription>
                    </div>
                    <div className="flex flex-col sm:flex-row gap-2">
                      <Button className="bg-[#b27c5a] hover:bg-[#9a6b4e] text-white">
                        <Plus className="mr-2 h-4 w-4" /> Nouvelle soutenance
                      </Button>
                      <Button variant="outline" className="border-[#b27c5a] text-[#b27c5a]">
                        <Download className="mr-2 h-4 w-4" /> Télécharger Excel
                      </Button>
                    </div>
                  </div>
                </CardHeader>
                <CardContent>
                  <Tabs defaultValue="all">
                    <TabsList className="mb-4">
                      <TabsTrigger value="all">Toutes ({soutenances.length})</TabsTrigger>
                      <TabsTrigger value="waiting">En attente ({soutenances.filter(s => s.statut === "En attente").length})</TabsTrigger>
                      <TabsTrigger value="planned">Planifiées ({soutenances.filter(s => s.statut === "Planifiée").length})</TabsTrigger>
                      <TabsTrigger value="completed">Terminées ({soutenances.filter(s => s.statut === "Terminée").length})</TabsTrigger>
                      <TabsTrigger value="canceled">Annulé ({soutenances.filter(s => s.statut === "Annulé").length})</TabsTrigger>
                    </TabsList>

                    <TabsContent value="all" className="space-y-4">
                      <div className="flex flex-col sm:flex-row justify-between gap-4">
                        <div className="relative">
                          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={18} />
                          <Input
                            placeholder="Rechercher par groupe, jury ou salle..."
                            className="pl-10 w-full sm:w-80"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                          />
                        </div>
                        <Button variant="outline" className="flex items-center">
                          <Filter className="mr-2 h-4 w-4" /> Filtres
                        </Button>
                      </div>

                      <div className="overflow-x-auto">
                        <table className="w-full border-collapse">
                          <thead>
                            <tr className="border-b dark:border-gray-700">
                              <th className="py-3 px-4 text-left font-medium text-gray-500 dark:text-gray-400">
                                <div className="flex items-center"><Calendar className="mr-2 h-4 w-4" /> Date</div>
                              </th>
                              <th className="py-3 px-4 text-left font-medium text-gray-500 dark:text-gray-400">
                                <div className="flex items-center"><Clock className="mr-2 h-4 w-4" /> Heure</div>
                              </th>
                              <th className="py-3 px-4 text-left font-medium text-gray-500 dark:text-gray-400">
                                <div className="flex items-center"><MapPin className="mr-2 h-4 w-4" /> Lieu</div>
                              </th>
                              <th className="py-3 px-4 text-left font-medium text-gray-500 dark:text-gray-400">
                                <div className="flex items-center"><Users className="mr-2 h-4 w-4" /> Groupes</div>
                              </th>
                              <th className="py-3 px-4 text-left font-medium text-gray-500 dark:text-gray-400">Jury</th>
                              <th className="py-3 px-4 text-left font-medium text-gray-500 dark:text-gray-400">Statut</th>
                              <th className="py-3 px-4 text-left font-medium text-gray-500 dark:text-gray-400">Actions</th>
                            </tr>
                          </thead>
                          <tbody>
                            {filteredSoutenances.map((soutenance) => (
                              <tr key={soutenance.id} className="border-b dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-800">
                                <td className="py-3 px-4">{soutenance.date}</td>
                                <td className="py-3 px-4">{soutenance.heure}</td>
                                <td className="py-3 px-4">{soutenance.lieu}</td>
                                <td className="py-3 px-4">
                                  <div className="flex flex-wrap gap-1">
                                    {soutenance.groupes.map((groupe) => (
                                      <span key={groupe} className="px-2 py-1 bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-100 text-xs rounded-full">
                                        {groupe}
                                      </span>
                                    ))}
                                  </div>
                                </td>
                                <td className="py-3 px-4">{soutenance.jury.join(" | ")}</td>
                                <td className="py-3 px-4">
                                  <span className={`px-2 py-1 text-xs rounded-full ${
                                    soutenance.statut === "Annulé" 
                                      ? "bg-red-100 dark:bg-red-900 text-red-700 dark:text-red-100"
                                      : soutenance.statut === "Terminée"
                                      ? "bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-100"
                                      : "bg-yellow-100 dark:bg-yellow-900 text-yellow-700 dark:text-yellow-100"
                                  }`}>
                                    {soutenance.statut}
                                  </span>
                                </td>
                                <td className="py-3 px-4">
                                  <div className="flex space-x-2">
                                    <button className="p-1 text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200">
                                      <Edit className="h-5 w-5" />
                                    </button>
                                    <button className="p-1 text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200">
                                      <Trash2 className="h-5 w-5" />
                                    </button>
                                  </div>
                                </td>
                              </tr>
                            ))}
                          </tbody>
                        </table>
                      </div>
                    </TabsContent>
                  </Tabs>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle className="text-2xl text-[#b27c5a]">Évaluation des étudiants</CardTitle>
                  <CardDescription>Attribuez des notes aux étudiants pour chaque critère d'évaluation</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="overflow-x-auto">
                    <table className="w-full border-collapse">
                      <thead>
                        <tr className="bg-gray-100 dark:bg-gray-800">
                          <th className="border border-gray-200 dark:border-gray-700 p-3 text-left">Groupe</th>
                          <th className="border border-gray-200 dark:border-gray-700 p-3 text-left">Étudiant</th>
                          <th className="border border-gray-200 dark:border-gray-700 p-3 text-left">Rapport</th>
                          <th className="border border-gray-200 dark:border-gray-700 p-3 text-left">Présentation</th>
                          <th className="border border-gray-200 dark:border-gray-700 p-3 text-left">Discussion</th>
                          <th className="border border-gray-200 dark:border-gray-700 p-3 text-left">Savoir-faire</th>
                          <th className="border border-gray-200 dark:border-gray-700 p-3 text-left">Total</th>
                          <th className="border border-gray-200 dark:border-gray-700 p-3 text-left">Remarque</th>
                          <th className="border border-gray-200 dark:border-gray-700 p-3 text-left">Action</th>
                        </tr>
                      </thead>
                      <tbody>
                        {students.map((student, index) => (
                          <tr
                            key={`${student.idSoutenance}-${student.idGroupe}-${student.idEtudiant}`}
                            className="border-b dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-800"
                          >
                            <td className="border border-gray-200 dark:border-gray-700 p-3">{student.nomGroupe}</td>
                            <td className="border border-gray-200 dark:border-gray-700 p-3">{student.etudiant}</td>
                            <td className="border border-gray-200 dark:border-gray-700 p-3">
                              <Input
                                type="number"
                                min="0"
                                max="20"
                                step="0.01"
                                value={student.noteRapport ?? ""}
                                onChange={(e) => handleInputChange(index, "noteRapport", e.target.value)}
                                className={`w-full ${validationErrors[`${index}-noteRapport`] ? "border-red-500" : ""}`}
                              />
                              {validationErrors[`${index}-noteRapport`] && (
                                <span className="text-red-500 text-xs">{validationErrors[`${index}-noteRapport`]}</span>
                              )}
                            </td>
                            <td className="border border-gray-200 dark:border-gray-700 p-3">
                              <Input
                                type="number"
                                min="0"
                                max="20"
                                step="0.01"
                                value={student.notePresentation ?? ""}
                                onChange={(e) => handleInputChange(index, "notePresentation", e.target.value)}
                                className={`w-full ${validationErrors[`${index}-notePresentation`] ? "border-red-500" : ""}`}
                              />
                              {validationErrors[`${index}-notePresentation`] && (
                                <span className="text-red-500 text-xs">
                                  {validationErrors[`${index}-notePresentation`]}
                                </span>
                              )}
                            </td>
                            <td className="border border-gray-200 dark:border-gray-700 p-3">
                              <Input
                                type="number"
                                min="0"
                                max="20"
                                step="0.01"
                                value={student.noteDiscussion ?? ""}
                                onChange={(e) => handleInputChange(index, "noteDiscussion", e.target.value)}
                                className={`w-full ${validationErrors[`${index}-noteDiscussion`] ? "border-red-500" : ""}`}
                              />
                              {validationErrors[`${index}-noteDiscussion`] && (
                                <span className="text-red-500 text-xs">
                                  {validationErrors[`${index}-noteDiscussion`]}
                                </span>
                              )}
                            </td>
                            <td className="border border-gray-200 dark:border-gray-700 p-3">
                              <Input
                                type="number"
                                min="0"
                                max="20"
                                step="0.01"
                                value={student.noteSavoirFaireSavoirEtre ?? ""}
                                onChange={(e) => handleInputChange(index, "noteSavoirFaireSavoirEtre", e.target.value)}
                                className={`w-full ${validationErrors[`${index}-noteSavoirFaireSavoirEtre`] ? "border-red-500" : ""}`}
                              />
                              {validationErrors[`${index}-noteSavoirFaireSavoirEtre`] && (
                                <span className="text-red-500 text-xs">
                                  {validationErrors[`${index}-noteSavoirFaireSavoirEtre`]}
                                </span>
                              )}
                            </td>
                            <td className="border border-gray-200 dark:border-gray-700 p-3 text-center font-medium">
                              {student.noteTotale !== undefined && !isNaN(student.noteTotale)
                                ? student.noteTotale.toFixed(2)
                                : "-"}
                            </td>
                            <td className="border border-gray-200 dark:border-gray-700 p-3">
                              <textarea
                                value={student.remarque ?? ""}
                                onChange={(e) => handleInputChange(index, "remarque", e.target.value)}
                                className="w-full p-2 border rounded dark:bg-gray-800 dark:border-gray-700"
                                rows={2}
                              />
                            </td>
                            <td className="border border-gray-200 dark:border-gray-700 p-3">
                              <div className="flex flex-col gap-2">
                                <Button
                                  onClick={() => handleSave(index)}
                                  disabled={updating === index || Object.keys(validationErrors).length > 0}
                                  className="w-full bg-blue-600 hover:bg-blue-700 text-white"
                                >
                                  {updating === index ? "..." : "Sauvegarder"}
                                </Button>
                                <Button
                                  onClick={() => handleUpdate(index)}
                                  disabled={updating === index || Object.keys(validationErrors).length > 0}
                                  className="w-full bg-[#b27c5a] hover:bg-[#9a6b4e] text-white"
                                >
                                  {updating === index ? "..." : "Mettre à jour"}
                                </Button>
                              </div>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </CardContent>
              </Card>
            </div>
          </main>
        </div>
      </div>
    </div>
  )
}